import {
  IonButton,
  IonContent,
  IonHeader,
  IonPage,
  IonTitle,
  IonToolbar,
} from "@ionic/react";
import { useUser } from "hooks/useUser";
import { useHistory } from "react-router";

const Tab3: React.FC = () => {
  const history = useHistory();
  const { logout } = useUser();
  const handleLogout = async () => {
    await logout();
    history.replace("/login");
  };

  return (
    <IonPage>
      <IonHeader>
        <IonToolbar>
          <IonTitle>Perfil</IonTitle>
        </IonToolbar>
      </IonHeader>
      <IonContent fullscreen>
        <IonHeader collapse="condense">
          <IonToolbar>
            <IonTitle size="large">Perfil</IonTitle>
          </IonToolbar>
        </IonHeader>

        <section>
          <IonButton
            color="danger"
            expand="block"
            fill="outline"
            className="m-4"
            onClick={handleLogout}
          >
            Sair
          </IonButton>
        </section>
      </IonContent>
    </IonPage>
  );
};

export default Tab3;
